@import './variables.css';

@font-face {
  font-family: var(--icon-font-family);
  src:
    url('./fonts/dojo2BaseTheme.ttf?nocf9g') format('truetype'),
    url('./fonts/dojo2BaseTheme.woff?nocf9g') format('woff'),
    url('./fonts/dojo2BaseTheme.svg?nocf9g#dojo2') format('svg');
  font-weight: normal;
  font-style: normal;
}

.icon {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: var(--icon-font-family) !important;
  speak: none;
  font-size: inherit;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: inherit;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

